import "./Step1.scss";
import { Button } from "../../components/Button/Button";
import { Input } from "../../components/Input/Input";
import { Select } from "../../components/Select/Select";
import { Sex } from "../../utils/enums";
import { Link, useNavigate } from "react-router-dom";
import { Formik, Form, Field } from "formik";
import stepper_1 from "../../images/stepper_1.svg";

import * as Yup from "yup";

export default function Step1() {
  const nicknameRegExp = /^[A-zА-яЁё0-9]*$/;
  const nameSurnameRegExp = /^[A-zА-яЁё]+$/;

  const navigateNextPage = useNavigate();

  return (
    <div className="step1__container">
      <img src={stepper_1} alt="step_1" />
      <Formik
        initialValues={{
          nickname: "",
          name: "",
          surname: "",
          sex: "",
        }}
        validationSchema={Yup.object({
          nickname: Yup.string()
            .max(30, "Максимальная длина 30 символов")
            .matches(nicknameRegExp, "Можно использовать только буквы и цифры")
            .required("Данное поле обязательно к заполнению"),
          name: Yup.string()
            .max(50, "Максимальная длина 50 символов")
            .matches(nameSurnameRegExp, "Можно использовать только буквы")
            .required("Данное поле обязательно к заполнению"),
          surname: Yup.string()
            .max(50, "Максимальная длина 50 символов")
            .matches(nameSurnameRegExp, "Можно использовать только буквы")
            .required("Данное поле обязательно к заполнению"),
          sex: Yup.string()
            .oneOf(["мужчина", "женщина"], "Invalid Job Type")
            .required("Required"),
        })}
        onSubmit={async (values, { setSubmitting }) => {
          await new Promise((r) => setTimeout(r, 500));
          setSubmitting(false);
          console.log(JSON.stringify(values, null, 2));
          navigateNextPage("/step2");
        }}
      >
        <Form>
          <div className="user-input">
            <Input
              label="Nickname"
              id="nickname"
              name="nickname"
              type="text"
              placeholder="Введите nickname "
            />
          </div>
          <div className="user-input">
            <Input
              label="Имя"
              id="name"
              name="name"
              type="text"
              placeholder="Введите имя"
            />
          </div>
          <div className="user-input">
            <Input
              label="Фамилия"
              id="surname"
              name="surname"
              type="text"
              placeholder="Введите фамилию"
            />
          </div>
          <div className="user-input">
            <Select label="Пол" id="sex" name="sex">
              <option value="">Выберите пол</option>
              <option value={Sex.MAN}>мужчина</option>
              <option value={Sex.WOMAN}>женщина</option>
            </Select>
          </div>
          <div className="navigation-buttons">
            <Link to="/">
              <Button>Назад</Button>
            </Link>
            <Button>Далее</Button>
          </div>
        </Form>
      </Formik>
    </div>
  );
}
